:root {
  font-size: 6.25%;
  scroll-behavior: smooth; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 16rem; }

h1,
h2,
h3,
h4,
h5,
h6,
fieldset {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none;
  color: inherit; }

ul {
  list-style: none; }

button {
  border: none;
  outline: none;
  box-shadow: none; }

.header {
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column; }
  .header__image {
    width: 50%;
    padding: 20px 0 10px 0; }

.form {
  width: 100%;
  padding: 0 30px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .form__search {
    padding: 8px;
    text-align: left;
    border-radius: 5px;
    border-color: #4383e2; }
  .form__btn {
    padding: 7px;
    color: yellow;
    font-size: 20px;
    cursor: pointer; }
    .form__btn:active {
      border: inset; }
